.about-mid {
	padding: 80px 30px 40px;
    height: 100vh;
    overflow: auto;
    overflow-x: hidden;
    top: -80px;
}

//== DEVICE SCREEN ==//
/*Device = 대부분의 스마트폰 모바일 기기(세로)*/
@media (min-width: 320px) and (max-width: 480px) {
	.about-mid {
		padding: 0 10px;
		height: initial;
   		top: initial;
	}
}

